.Team{
  font-family: 'owo';
}
.menu{
  font-family: 'owo';
  display: flex;
  align-items: center;
  flex-direction: column;
  min-height: 100vh;
  font-size: 10px;
  color: #f5f5f5;
}
.owo{
  background: #ffffff;
  display: flex;
  align-items: center;
  flex-direction: column;
  min-height: 100vh;
  font-size: 10px;
  color: #f5f5f5;
}

.menubar{
  z-index: 1;
  position: absolute;
  background-color: #003ab68c;
  width: 1920px;
  height: 100px;
  box-shadow: 0 1px 5px #000000;
}
.about{
  z-index: 1;
  font-size: 18px;
  position: absolute;
  top: 40px;
  right: 950px;
  color: #ffffffaf;
}

.about:link {text-decoration: none; color: #e6e6e67a;}
.about:hover {text-decoration: none; color: #ffffffd8;}
@font-face {
  font-family: 'owo';
  src: url('./font/AppleSDGothicNeoB.ttf');
}